:root{
  --ah-theme-color:#20dab2;
  --ah-shadow-color:#20dab267;
}
body {
  background-color: #f6f7f9;
  height:100%;
  font-size:.9rem;
}
.property{
  position:relative;
}

.property .ah-title{
  font-size:1.2rem;
  font-weight: bold;
}

.property .img-wrap{
  height:150px;
}

.property .img-wrap img{
  height:100%;
  width:100%;
  object-fit: cover;
}

.property .market-type{
  text-transform: capitalize;
  position:absolute;
  top:10px;
  left:-16px;
  padding:4px 10px;
  background:var(--ah-theme-color);
  color:#fff;
  font-size:.8rem;
  box-shadow:0 1px 10px var(--ah-shadow-color);
}

@media (max-width:992px){
  body{
    margin:10px;
  }
  .property .market-type{
    left:10px;
  }
}

.property .favorite{
  position:absolute;
  top:15px;
  right:18px;
}

.property .divider{
   margin:10px 0 10px;
   border-top:1px solid #eee;
}

/* -- Single Property -- */
.map-container {
  height: 200px;
}